export default {
  name: "create-blog",
  mounted() {},
  data() {
    return {
      newBlog: {},
    };
  },
  computed: {
    profile() {
      return this.$store.state.profile;
    },
  },
  methods: {
    createBlog() {
      swal("Congrats on your new Blog!").then((value) => {
        this.$store.dispatch("create", { data: this.newBlog, path: "blogs" });
        swal(`Keep on keeping on!`);
      });
    },
  },
  components: {},
};
